import { px } from '../utilities/px'

let ID = 0;

export class Details {
  summary: HTMLElement | null

  content: HTMLElement | null

  animation: Animation | null

  isClosing: boolean

  isExpanding: boolean

  constructor(public el: HTMLDetailsElement) {
    const index = ID++
    this.el.dataset.id = `${index}`
    this.el.id = `details-${index}`

    // Armazena o elemento <summary>
    this.summary = el.querySelector('.details__summary')

    // Armazene o elemento <div class="details__content">
    this.content = el.querySelector('.details__content')

    // Armazena o objeto de anima√ß√£o (para que possamos cancel√°-lo se necess√°rio)
    this.animation = null

    // Armazena se o elemento est√° fechando
    this.isClosing = false

    // Armazena se o elemento est√° se expandindo
    this.isExpanding = false

    // Detecta cliques do usu√°rio no elemento de resumo
    if (this.summary) {
      this.summary.onclick = (e) => this.onClick(e)
    }
  }

  onClick(e: MouseEvent) {
    // Interrompe o comportamento padr√£o do navegador
    e.preventDefault()

    // Adicione um estouro em <detalhes> para evitar o estouro de conte√∫do
    this.el.style.overflow = 'hidden'

    // Verifique se o elemento est√° sendo fechado ou j√° est√° fechado
    if (this.isClosing || !this.el.open) {
      this.open()

      // Verifique se o elemento est√° sendo aberto ou j√° est√° aberto
    } else if (this.isExpanding || this.el.open) {
      this.close()
    }
  }

  close() {
    // Defina o elemento como "sendo fechado"
    this.isClosing = true

    // Armazena a altura atual do elemento
    const startHeight = px(this.el.offsetHeight)

    // Calcule a altura do resumo
    let endHeight = this.summary ? px(this.summary?.offsetHeight) : px(75)

    // Se j√° houver uma anima√ß√£o em execu√ß√£o
    if (this.animation) {
      // Cancela a anima√ß√£o atual
      this.animation.cancel()
    }

    // Iniciar uma anima√ß√£o WAAPI
    this.animation = this.el.animate(
      {
        // Defina os keyframes de startHeight a endHeight
        height: [startHeight, endHeight],
      },
      {
        duration: 400,
        easing: 'ease-out',
      }
    )

    // Quando a anima√ß√£o estiver conclu√≠da, chame onAnimationFinish ()
    this.animation.onfinish = () => this.onAnimationFinish(false)

    // Se a anima√ß√£o for cancelada, a vari√°vel isClosing √© definida como falsa
    this.animation.oncancel = () => (this.isClosing = false)
  }

  open() {
    // Aplicar uma altura fixa no elemento
    this.el.style.height = px(this.el.offsetHeight)

    // For√ßa o atributo [abrir] no elemento de detalhes
    this.el.open = true

    this.el.dispatchEvent(new CustomEvent('expand', { detail: this.el.id }));
    
    // Aguarde o pr√≥ximo quadro para chamar a fun√ß√£o de expans√£o
    requestAnimationFrame(() => this.expand())
  }

  expand() {
    // Defina o elemento como "em expans√£o"
    this.isExpanding = true
    
    // Obtenha a altura fixa atual do elemento
    const startHeight = px(this.el.offsetHeight)

    // Calcule a altura aberta do elemento (altura do resumo + altura do conte√∫do)

    const endHeight =
      this.summary && this.content
        ? px(this.summary.offsetHeight + this.content.offsetHeight)
        : px(75)

    // Se j√° houver uma anima√ß√£o em execu√ß√£o
    if (this.animation) {
      // Cancela a anima√ß√£o atual
      this.animation.cancel()
    }

    // Iniciar uma anima√ß√£o WAAPI
    this.animation = this.el.animate(
      {
        // Defina os quadros-chave de startHeight a endHeight
        height: [startHeight, endHeight],
      },
      {
        duration: 400,
        easing: 'ease-out',
      }
    )

    // Quando a anima√ß√£o estiver conclu√≠da, chame onAnimationFinish ()
    this.animation.onfinish = () => this.onAnimationFinish(true)

    // Se a anima√ß√£o for cancelada, a vari√°vel isExpanding √© definida como falsa
    this.animation.oncancel = () => (this.isExpanding = false)
  }

  onAnimationFinish(open: boolean) {
    // Defina o atributo aberto com base no par√¢metro
    this.el.open = open

    // Limpa a anima√ß√£o armazenada
    this.animation = null

    // Redefinir isClosing & isExpanding
    this.isClosing = false

    this.isExpanding = false

    // Remova o estouro oculto e a altura fixa
    this.el.style.height = this.el.style.overflow = ''
  }
}

declare global {
  interface HTMLElementEventMap {
    'expand': CustomEvent<string>;
  }
}